//
.position-rel {
    position: relative;
}

.position-abs {
    position: absolute;
}

.position-fixed {
    position: fixed;
}

.position-sticky{
    position: sticky;
}

//

.pos-abs-0 {
    position: absolute;
    top: 0;
    left: 0;
}

.pos-abs-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.pos-abs-100 {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

//

.pos-fixed-100 {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}

.pos-fixed-100per {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

// 

.pos-sticky-from-header{
    position: sticky;
    top: 56px;
}

/* ------------- */
.bottom-100per {
    bottom: 100%;
}

/* ------------- */
.top-100per {
    top: 100%;
}

/* ------------- */
.left-0 {
    left: 0;
}

.left-50per {
    left: 50%;
}

/* ------------- */
.right-0 {
    right: 0;
}
/* ------------- */
.top-0{
    top: 0;
}

/* ------------- */
.x-center {
    left: 50%;
    transform: translateX(-50%);
}


/* ------- Z INDEX ------ */

.z-index-lv3{
    z-index: var(--z-index-lv3);
}

.z-index-lv5{
    z-index: var(--z-index-lv5);
}
